import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";
import Image from "../../../../../Components/UI/Image/image";

const Arrays = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>What is Sets and How to create Sets | Collection Data type in Swift </title>
                <meta name="description" content=" "/>
                <meta name="keywords" content="sets,collection,swift,data,type,overview,create,line,number,iOS,swift,tutorial,video,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/sets"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift"
                activate = {[2,9]} // Section, Row
                state = {props.state}>

                <h1 className = "subHeader">Swift Sets</h1>
                <h2 className = "header">What is a Swift Set and how to create?</h2>
                <AuthorInfo
                        version = "Xcode 12.2, Swift 5.3"/>
                <p>Set is an unordered collection of <strong>unique</strong> value of the same data 
                  type. Unlike array which is ordered collection. <strong>Set is useful when you want to keep 
                  value that are unique and doesn't appear more than once and the order is not important. </strong>
                  For example, the image below shows a set of ingredients for some food.</p>
                <Image
                    src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/Sets/Set%20ingredients.png?raw=true"
                    alt = "Sets of ingredents in Swift"
                    size = "W200"/>

                <h4>Topics:</h4>
                <ol>
                    <li><a href = "/reference/swift/sets#create">How to create a Sets?</a></li>
                    <li><a href = "/reference/swift/sets#access">How to access elements in Sets?</a></li>
                    <li><a href = "/reference/swift/sets#access">How to find elements in Sets?</a></li>
                    <li><a href = "/reference/swift/sets#modify">How to modify a Set? (Adding and Removing)</a></li>
                    <li><a href = "/reference/swift/arrays#slicing">How to combine Sets ?</a></li>
                    <li><a href = "/reference/swift/arrays#methods">Sets' methods and properties</a></li>
                </ol>
                <h2 id = "create" className = "header2">Creating sets in Swift.</h2>
                <p>Set does not have their own literals like <strong>array literals</strong>. When creating a set you use 
                   array literals and each element is separate by comma. However, you have to explicitly declare the variable
                   if not an array will be created.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredientsArray</span> = [<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>]<br/>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredientsSet</span>: <span className = "purple">Set</span> = [<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>]
                            <span classNam = "grey">// You have to explicaitly declare an set</span>
                        <br/>
                    </Fragment>} />

                <p>You can also create an <strong>Set Object</strong> by using <strong>Set([  ])</strong> and the compiler 
                   is able to infer the data type.</p>
                <SwiftCode 
                    code = {
                   <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients</span> = <span className = "purple">Set</span>([<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>])<br/>
                    </Fragment>} />

                <h2 id = "create" className = "header2">Accessing elements in sets.</h2>
                <p>Accessing is not to extract the element out like what we did in <a href = "/reference/swift/arrays#access">array </a>
                  and <a href = "/reference/swift/Dictionary#access">dictionary</a>. But is used to check whether is a element or a 
                  set of element is inside or not inside another set.</p>
                <h3>.contains()</h3>
                <p>.contains method is used to check if a set contains s pecific element. It return true, if it contains that 
                   element and false if it not.</p>
                <SwiftCode 
                    code = {
                   <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients</span> = <span className = "purple">Set</span>([<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>])<br/>
                        <span className = "blue">ingredients</span>.<span className = "purple">contains</span>(<span className = "blue">"Salt"</span>)
                    </Fragment>} />

                <h3>.isSubset()</h3>
                <p>.isSubset() method is used to check if a set contains all the element of another set.
                   For example, Set A is a subset of set B if all the elements in Set A are found in Set B.</p>
                <Image
                    src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/Sets/isSubset%20Swift.png?raw=true"
                    alt = "Sets of ingredents in Swift"
                    size = "W300"/> <br/>
                <SwiftCode 
                    code = {
                   <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients</span> = <span className = "purple">Set</span>([<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>])<br/>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients2</span> = <span className = "purple">Set</span>([<span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>])<br/>
                        <span className = "blue">ingredients2</span>.<span className = "purple">isSubset</span>(of: <span className = "blue">ingredients</span>)
                    </Fragment>} />

                <h3>.isSuperSubset()</h3>
                <p>.isSuperSubset() method is used to check if a portion of a set contains all the element of another set. 
                   For example, Set B is a superset of set A if all the elements in set A are the element of set B as shown 
                   in the image below.</p>
                <Image
                    src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/Sets/isSuperset%20Swift.png?raw=true"
                    alt = "Sets of ingredents in Swift"
                    size = "W300"/> <br/>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients</span> = <span className = "purple">Set</span>([<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>])<br/>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients2</span> = <span className = "purple">Set</span>([<span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>])<br/>
                        <span className = "blue">ingredients</span>.<span className = "purple">isSuperset</span>(of: <span className = "blue">ingredients2</span>)
                    </Fragment>} />

                <h3>.isDisjoint()</h3>
                <p>.isDisjoint() method is used to check whether two sets contains different elements as shown in the image below</p>
                <Image
                    src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/Sets/is%20disjoint%20Swift.png?raw=true"
                    alt = "Sets of ingredents in Swift"
                    size = "W500"/> <br/>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients</span> = <span className = "purple">Set</span>([<span className = "red">"Garlic"</span>, <span className = "red">"Salt"</span>, <span className = "red">"Tomato"</span>, <span className = "red">"Chicken"</span>, <span className = "red">"Pepper"</span>])<br/>
                        <span className = "darkpurple">let</span> <span className = "blue">ingredients2</span> = <span className = "purple">Set</span>([<span className = "red">"Beef"</span>, <span className = "red">"Wine"</span>, <span className = "red">"Water"</span>])<br/>
                        <span className = "blue">ingredients</span>.<span className = "purple">isDisjoint</span>(with: <span className = "blue">ingredients2</span>)
                    </Fragment>} />

                <h2 id = "create" className = "header2">Finding Elements in sets</h2>
                <h3>first and .firstIndex</h3>
                <h2 id = "create" className = "header2">Modifying set in Swift</h2>
                <h2 id = "create" className = "header2">Combining sets</h2>
                <h2 id = "create" className = "header2">Sets' methods and properties</h2>
                
            </WithSidebar>
        </Fragment>
    )
};

export default Arrays;
