import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";
import Image from "../../../../../Components/UI/Image/image";

const Arrays = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>What is Dictionary and How to create Dictionary in Swift | Collection Data type in Swift </title>
                <meta name="description" content="Array is a collection data type when you can store mutiple 
                data or values. Array are zero indexed which means you count them starting from zero and the 
                the second element is 1. You create an array using square braket [ ] and any value inside 
                the array is separate by a comma. "/>
                <meta name="keywords" content="array,collection,swift,data,type,overview,create,line,number,iOS,swift,tutorial,video,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/Dictionary"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift"
                activate = {[2,8]} // Section, Row
                state = {props.state}>

                <h1 className = "subHeader">Swift Dictionary</h1>
                <h2 className = "header">What is a Swift Dictionary and how to create?</h2>
                <AuthorInfo
                        version = "Xcode 12.2, Swift 5.3"/>
                <p>Unlike <a href = "/reference/swift/arrays">Array</a> which is ordered collection, dictionary 
                   is another collection but is it <strong>unordered</strong>. Dictionary contains a 
                   <strong> keys</strong> and <strong>value</strong>. The key cannot be appear more than once but different key can have 
                   the same value. These keys help you to identify which value it is related to. As 
                   shown in the image below: </p>

                <p>The image below shown a key which is the name of the person and the key that linked to it 
                   is the values which is their age. For example, from here, we known that Tylor is 28 years old and 
                   Wes is 24.</p>
                <Image
                src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/DIctionary/Keys%20and%20Values.png?raw=true"
                alt = "Dictionary's keys and values in Swift"
                size = "W600"/>

                <h3>When should you use dictionary?</h3>
                <p>Dictionary is useful when you want to get the value by it identifier or name. 
                   The main different between array and dictionary is that array fetch a value by 
                   it index which is sequential while dictionary fetch by it keys and the keys 
                   doesn't have any order in it.</p>

                
                <h4>Topics:</h4>
                <ol>
                    <li><a href = "/reference/swift/Dictionary#create">How to create a Dictionary?</a></li>
                    <li><a href = "/reference/swift/Dictionary#access">How to access the values?</a></li>
                    <li><a href = "/reference/swift/Dictionary#modify">How to modify an array? (Adding, Removing and Updating)</a></li>
                    <li><a href = "/reference/swift/Dictionary#method">Array methods and properties</a></li>
                </ol>
                <h2 id = "create" className = "header2">How to create a Dictionary?</h2>
                <p>To create an dictionary in Swift, you use square brackets and inside the square brackets
                   it contains key-value paris and is separated by commas. As you know Swift will infer the 
                   data type and the example below is [String: Int]</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                    </Fragment>} />

                <p>To create an <strong>empty</strong> dictionary, you can required to state the data type explicitly.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ages</span>: [<span className = "purple">String</span>:<span className = "purple">Int</span>] = [:]<br/>
                    </Fragment>} />

                <h2 id = "access" className = "header2">How to access the values?</h2>
                <p>Accessing the value in dictionary is similar to Array using <strong>subscript</strong> or <strong>square brackets</strong>.
                   However, you don't access the value by its index but rather you call its <strong>keys</strong>. For example,
                   you want to get Wes' age and you can type: </p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        print(<span className = "blue">ages</span>[<span className = "red">"Wes"</span>])
                    </Fragment>}
                    result = "24" />
                <h2 id = "modify" className = "header2">How to modify an array? (Adding, Removing and Updating)</h2>
                <h3>Updating and Adding Values</h3>
                <p>You can change the value by using <strong>subscripting</strong> or <strong>.updateValue(Value, forKey: Key)</strong>.
                   If the key doesn't exist, this method will add a new pair of value in.</p>
                
                <p>For example, Wes's age is enter incorrectly and you can change his age by typing this (below) if 
                   the keys exist the value will change. (The example below uses subscripting as an example). 
                   <i> This method can also be used to create a new key and value pair.</i></p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        <span className = "blue">ages</span>[<span className = "red">"Wes"</span>] = <span className = "darkblue">20</span> <br/>
                        print(<span className = "blue">ages</span>)
                    </Fragment>}
                    result = '["Wes": 20, "Evan": 20, "Jes": 5, "Tylor": 28]' />
                
                <p>For example, Chua is 22 year old and you can add him by typing this (below) if 
                   the key does not exist, new key will be created. (The example below uses .updateValue as an example).
                   <i> This method can also used to update a value.</i></p>

                <p>As you can see, chua is added anywhere in the dictionary and not the last one. 
                  This is becuase Dictionary is an <strong>unordered collection</strong>.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        <span className = "blue">ages</span>.updateValue(<span className = "darkblue">22</span>, forKey: <span className = "red">"chua"</span>) <br/>
                        print(<span className = "blue">ages</span>)
                    </Fragment>}
                    result = '["Evan": 20, "chua": 22, "Wes": 24, "Tylor": 28, "Jes": 5]' />

                <h3>Removing a Key and Value Pair</h3>
                <p>You can remove a value by using <strong>.subscripting</strong>  and assign a value of <strong>.nil</strong> to it. Moreover, 
                   you can use <strong>.removeValue(forKey: Key)</strong> to remove a pair.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        <span className = "blue">ages</span>[<span className = "red">"Evan"</span>] = <span className = "purple">nil</span> <br/>
                        print(<span className = "blue">ages</span>)
                    </Fragment>}
                    result = '["Wes": 24, "Jes": 5, "Tylor": 28]' />
                <br/>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Evan"</span>: <span className = "darkblue">20</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        <span className = "blue">ages</span>.removeValue(forKey: <span className = "red">"Evan"</span>) <br/>
                        print(<span className = "blue">ages</span>)
                    </Fragment>}
                    result = '["Wes": 24, "Jes": 5, "Tylor": 28]' />
                
                <h2 if = "method" className = "header2">Array methods and properties</h2>
                <h3>.count</h3>
                <p>.count is a dictionary's property and it is used to count the number of key-value paris 
                   in the dictionary.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        print(<span className = "blue">ages</span>.<span className = "purple">count</span>)
                    </Fragment>}
                    result = '3' />
                
                <h3>.isEmpty</h3>
                <p>.isEmpty is a dictionary's property and it is used to check whether is the dictionary is 
                   empty.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        print(<span className = "blue">ages</span>.<span className = "purple">isEmpty</span>)
                    </Fragment>}
                    result = 'false' />

                <h3>.reserveCapacity(Int)</h3>
                <p>.reserveCapacity can be use to increase the performance if you know how much the data, the 
                   dictionary will be storing. .reserveCapacity(Int) take in a <strong>parameter</strong> which 
                   is a Int and it indicated the size of the dictionary.</p>

                <p>The reason to add this is becuase newly allocated storage might be greater than the 
                   requested capacity if Swift determine the size by itself. By stating it, you tell Swift 
                   the size of the minimum capacity and Swift will not allocate more space for it. 
                   Therefore, <strong>avoiding over allocation</strong>.</p>
                <SwiftCode 
                    code = {
                    <Fragment>
                        <span className = "darkpurple">var</span> <span className = "blue">ages</span> = [<span className = "red">"Wes"</span>: <span className = "darkblue">24</span>, <span className = "red">"Jes"</span>: <span className = "darkblue">5</span>, <span className = "red">"Tylor"</span>: <span className = "darkblue">28</span>]<br/>
                        <span className = "blue">ages</span>.<span className = "purple">reserveCapacity</span>(<span className = "darkblue">5</span>)
                    </Fragment>}
                    result = 'false' />
            
            </WithSidebar>
        </Fragment>
    )
};

export default Arrays;
